  font-weight: 500;
  padding: 15px 15px;
}
.login_mobile_no{
  border: 0px !important;
}
 
.login_mobile_no>div{
  border-radius: 10px;
}
.login_mobile_no>div:before, .login_mobile_no>div:hover , .login_mobile_no>div:after{
